/* Setting up fonts */
@font-face {
  font-family: "oswald";
  src: url("../fonts/oswald.woff") format("woff");
}
@font-face {
  font-family: "nunito";
  src: url("../fonts/nunito-regular.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "nunito";
  src: url("../fonts/nunito-semibold.woff") format("woff");
  font-weight: 600;
}
@font-face {
  font-family: "nunito";
  src: url("../fonts/nunito-bold.woff") format("woff");
  font-weight: 700;
}
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: #031f47;
  --secondary-color: #bb881b;
  --title-font: "oswald";
  --body-font: "nunito";
}

h1,
h2,
h3 {
  font-family: "oswald";
  letter-spacing: 1.4;
}

p,
li,
span {
  font-family: "nunito";
  letter-spacing: 1;
}

/* Remove default margin */
/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.6;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
.container {
  width: 90%;
  max-width: 1300px;
  margin: 0 auto;
}

.nav-toggle {
  cursor: pointer;
  border: 0;
  width: 3em;
  height: 3em;
  padding: 0em;
  border-radius: 50%;
  transition: opacity 250ms ease;
  position: absolute;
  left: 0;
  transform: translateY(0%);
}

.nav-toggle:focus,
.nav-toggle:hover {
  opacity: 0.75;
}

.hamburger {
  width: 50%;
  position: relative;
}

.hamburger,
.hamburger::before,
.hamburger::after {
  display: block;
  margin: 0 auto;
  height: 3px;
  background: var(--primary-color);
}

.hamburger::before,
.hamburger::after {
  content: "";
  width: 100%;
}

.hamburger::before {
  transform: translateY(-6px);
}

.hamburger::after {
  transform: translateY(3px);
}

.logo-image {
  width: 140px;
}

.header {
  background-color: #eee;
  padding: 0.5em 0;
  text-align: center;
}

.nav {
  max-height: 0;
  overflow: hidden;
  transition: max-height 250ms;
}

.nav-list {
  margin-top: 1em;
}

.nav-item {
  list-style-type: none;
  margin: 0.5em 0 0.5em 0;
}

.nav-link {
  text-decoration: none;
  text-transform: uppercase;
  color: var(--primary-color);
  font-family: var(--title-font);
  font-weight: 700;
  font-size: 1.125rem;
  cursor: pointer;
}